<template>
    <div>
        <!-- 头部 -->
        <div class="header" v-show="!flag">
             <van-button class="arrowleftgo" @click="$router.go(-1)"   plain hairline round icon="arrow-left" type="default" />
             <van-button class="arrowright" plain hairline round icon="ellipsis" type="default" />
        </div>
            <van-nav-bar
            title="商品详情"
            left-arrow
            @click-left="onClickLeft"
            left-text="返回"
            placeholder
            fixed
            v-show="flag"
            />
        <!-- 头部结束 -->
        <!-- 轮播图 -->
        <div class="banner">
             <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <van-swipe-item v-for="(item,index) in banner" :key="index">           
                    <img :src="item" alt="">
                </van-swipe-item>
            </van-swipe>   
        </div>
        <!-- 轮播结束 -->

        <!-- 活动图片 -->
        <div>
            <img src="../assets/images/618.jpg" alt="">
        </div>
        <!-- 活动图片 结束 -->

        <!-- 描述内容 -->
        <div class="content">
            <div class="top">
                <van-row>
                    <van-col span="20">
                        <div class="price">
                            <span>{{goods.price}}</span>
                        </div>
                    </van-col>
                    <!-- <van-col span="4">
                        <div class="price02">
                            <p><img src="../assets/images/money.png" alt=""></p>
                            <p><span>降价提醒</span></p>
                        </div>
                    </van-col> -->
                    <van-col span="4">
                        <div class="price03">
                            <p><van-icon :name="name" color = "red" @click="collect"/></p>
                            <p>收藏</p>
                        </div>
                    </van-col>
                </van-row>
            </div>
            <div class="center">
                <p class="title">{{goods.name}}</p>
            </div>
            <div class="bottom">
                <p>【小米直供|限时直降】赠：5元晒单红包+品质音乐耳机+高清钢化膜+运费险【小米cc9限时钜惠】<a href="">查看</a> </p>
            </div>
        </div>
        <!-- 描述内容结束 -->

        <!-- 优惠券 -->
        <div class="coupon">
            <van-cell is-link @click="showPopup" >
                <div class="main">
                    <h3>优惠</h3>
                    <div class="right">
                        <p><span class="icon">赠品</span>小米cc9pro手机贴膜（图片仅供参考） 品质 好礼 X1，高品质音乐耳机 商家仓  图片仅供参考 以实物为准 (新) 图片色 X1</p>
                        <p><span class="icon">免费领</span> 体验卡免费领，超大流量任性用</p>
                    </div>
                </div>

            </van-cell>
            <van-popup v-model="show"  position="bottom" :style="{ height: '30%' }">
                <h1 style="text-align:center ;margin-top:10px">优惠无</h1>
            </van-popup>
        </div>
        <!-- 优惠券结束 -->

        <!-- 商品选择 -->
        <div class="shopmain">
            <div class="top">
                <h3>已选</h3>
                <div class="des">
                    <p class="text01">魔法绿境，6G+128G 全网通，1个</p>
                    <p class="text02">本商品支持保障服务，点击可选服务</p>
                </div>
            </div>
           
            <div class="bottom">
                 <h3>运费</h3>
                <div class="des">
                    <p class="text01">在线支付免运费</p>
                </div>
            </div>
        </div>
        <!-- 商品选择结束 -->

        <!-- 服务 -->
        <div class="serve">
            <p><span class="licon"></span>商家发货&售后</p>
            <p><span class="licon"></span>7天无理由退款（激活后不支持）</p>
            <p><span class="licon"></span>送运费险<span class="licon"></span>一年质保<span class="licon"></span>全国联保</p>
        </div>
        <!-- 服务 结束-->
        
        <!-- 商品介绍 -->
        <div class="descrip">
            <!-- <van-nav-bar
                title="商品详情"
            /> -->
            <p v-for="(item,index) in desc" :key="index">
                <img :src="item" alt="">
            </p>
        </div>
        <!-- 商品介绍结束 -->

        <!-- 商品导航 -->
        <van-goods-action>
            <van-goods-action-icon icon="home-o" text="首页" color="#ff5000"  @click="tohome"/>
            <van-goods-action-icon icon="cart-o" text="购物车" :badge="num" color="#ff5000"  @click="tocart"/>
            <van-goods-action-icon icon="qr" text="我的收藏" color="#ff5000" @click="tocollect"/>
            <van-goods-action-button type="warning" text="加入购物车" @click="add(goods)"/>
            <van-goods-action-button type="danger" text="立即购买" to="/cart"  @click="add(goods)"/>
        </van-goods-action>
        <!-- 商品导航结束 -->


    </div>
</template>

<script>
    import {createNamespacedHelpers} from "vuex";
    const {mapState,mapMutations} = createNamespacedHelpers("cart");


    export default {
        data(){
            return {
                flag:false,
                // 轮播
                banner:[
                     require("../assets/images/mi01.webp.jpg"),
                     require("../assets/images/mi02.webp.jpg"),
                     require("../assets/images/mi03.webp.jpg"),
                     require("../assets/images/mi04.webp.jpg"),
                     require("../assets/images/mi05.webp.jpg"),
                ],
                // 商品详情
                desc:[
                    require("../assets/images/details01.jpg"),
                     require("../assets/images/details02.jpg"),
                     require("../assets/images/details03.jpg"),
                     require("../assets/images/details04.jpg"),
                ],
                show:false,
                goods:{
                    id:2,
                    name:"小米cc9pro手机 1亿像素 五摄四闪 10倍混合光学变焦 魔法绿境 6G+128G 全网通",
                    price:"2498",
                    num:1,
                    imgarr:require('../assets/images/mi01.webp.jpg'),
                },
                name:"like-o"
            }
        },
        methods:{
            collect(){
                if(this.name == "like-o"){
                    this.name = "like"
                    this.$store.commit("cart/collect",this.goods)
                    
                }else{
                    this.name = "like-o"
                     this.$store.commit("cart/coldel")
                }
                

            },
             onClickLeft() { 
                this.$router.go(-1)
            },
             showPopup() {
                this.show = true;
            },
            // 点击回到首页
            tohome(){
                this.$router.push("/home")
            },
            // 点击跳转到购物车
            tocart(){
                this.$router.push("/cart")
            },
            tocollect(){
                this.$router.push("/collect")
            },
            // 调用vuex中的add方法
            ...mapMutations(["add"])
        },
        computed:{
            ...mapState({
                num:state=>state.num
            })
        },
        mounted(){
            // 页面滚动监视
            window.addEventListener("scroll",()=>{
                if(window.pageYOffset >100){
                   this.flag = true
                }else{
                    this.flag = false
                }
            })
        }
    }
</script>

<style lang="scss" scoped>

.header{
    
    .arrowleftgo,
    .arrowright{
        position:fixed;   
        top:10px;
        z-index: 200;
        font-size: 16px;
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        border-radius: 50%;
        color: white;
        background-color: #666666;
    }
    .arrowleftgo{
        left:10px;
    }
    .arrowright{
        right: 10px;
    }
}
.banner{
    background-color: white;
    padding-top: 10px;
}

// 描述内容
.content{
    margin-top: 10px;
    padding: 0 15px;
    background-color: white;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    .top{
        position: relative;
        .price{
            font-size: 30px;
            color: red;
            font-weight: bold;
            margin-left:15px ;
            &::before{
                content: "￥";
                position: absolute;
                left: 0;
                top: 16px;
                font-size: 16px;
                color: red;
            }
             &::after{
                content: ".00";
                position: absolute;
                left:84px;
                top: 16px;
                font-size: 16px;
                color: red;
            }
        }
        .price02,
        .price03{
            p{
                margin-right:5px ;
                text-align: center;
                font-size: 14px;
                // img{
                //     width: 14px;
                //     display: block;
                //     margin: 0 auto;
                // }

            }
        }
    }
    .center{
        .title{
            font-size: 16px;
            font-weight: bold;
            color: #000;
            &::before{
                content: "618";
                display: inline-block;
                padding: 0 8px;
                background-color: red;
                color: white;
                font-size: 8px;
                margin-right: 5px;
            }
        }
    }
    .bottom{
        margin-top: 10px;
        padding-bottom:15px ;
        a{
            text-decoration: underline;
            color: red;
        }
    }
}
// 描述内容结束
// 优惠券
.coupon{
    margin-top: 15px;
    .van-cell {
        border-radius: 20px;
    }
    .main{
        text-align: center;
        display: flex;
        h3{
            width: 100px;
            font-weight: bold;
            margin-top:4px ;
        }
        .right{
            p{
                color: #353e45;
                display: inline-block;
                width: 200px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                height:30px;
                line-height:30px;

                .icon{
                    border: 1px solid #ea6668;
                    color: #ea6668;
                    margin-right: 5px;
                }
            }
        }
    }
}
// 优惠券结束

// 商品选择
.shopmain{
    border-top-left-radius: 20px;
    border-top-left-radius: 20px;
    background-color: white;
    margin-top: 15px;
    padding: 8px ;
    .top,
    .center,
    .bottom{
        display: flex;
        margin-bottom: 15px;
        h3{
            width: 60px;
            text-align: center;
            // margin-top:4px ;
            font-size: 15px;
        }
        .des{  
            flex: auto; 
            // width: 230px;
            font-size: 15px;
             padding-left: 20px;
             margin-left: 2px;
            .text02{
                font-size: 13px;
                color: #999999;
                padding-right: 20px;
                padding-left: 20px;
                // width: 230px;
            }
        }
    }
    .center{
        h3{
             width:80px;
            // text-align: center;
            margin-left:10px ;
            font-size: 15px;
        }
    }
    .bottom{
        padding-top: 15px;
    }
}
// 商品选择结束
// 服务
.serve{
    padding: 10px 15px;
    color: #909292;
    font-weight: bold;
    background-color: #fafafa;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    font-size: 14px;
    .licon{
        display: inline-block;
        width: 18px;
        height: 18px;
        background-image: url("../assets/images/gou.png");
        vertical-align: middle;
    }
}
// 服务结束

</style>